import React, { Component } from "react";
import Nav from "react-bootstrap/Nav";
//import Navbar from "react-bootstrap/Navbar";
import { updateUser, logoutUser } from "../actions/userAction";
import { connect } from "react-redux";
import { Link } from "react-router-dom";
import NavDropdown from "react-bootstrap/NavDropdown";
//import { Col } from "react-bootstrap";
import { AppBar, Toolbar, Typography, Button, Grid } from "@material-ui/core";


/*const body = {
  fontFamily: "charter, Georgia, Cambria, 'Times New Roman', Times, serif",
  color: "black"
};*/

class Navig extends Component {
  constructor(props) {
    super(props);
    this.state = {
      isLogged: this.props.isLogged,
      userName: "",
      imageUrl: "",
    };
    this.props.updateUser();
    this.logout = this.logout.bind(this);
  }

  logout() {
    console.log("Logout try");
    this.props.logoutUser();
    this.setState({
      isLogged: false,
      userName: "",
    });
  }

  render() {

    if (!this.props.isLogged) {
      return (
        <div >
          <AppBar position="static" style={{ backgroundColor: "#FFC017", paddingBottom: "5em", boxShadow:"none"  }}>
            
              <Grid container xs={12} md={12} lg={12} style={{position: "fixed", backgroundColor: "inherit", display: "block", zIndex: "100"}}>
              <Toolbar style={{ flexGrow: 1, borderBottom: "1px solid black" , borderColor: "black" }}>
                <Grid item xs={2} md={2} lg={2}></Grid>
                <Grid item xs={2} md={2} lg={2}>
                  {" "}
                  <Typography variant="h3" style={{ color: "black"}}>
                    <Link
                      exact
                      to="/medfrontend/"
                      style={{ fontWeight: "bold", color: "black" }}
                    >
                      MedBook
                    </Link>
                  </Typography>
                </Grid>
                <Grid item xs={4} md={4} lg={4} ></Grid>
                <Grid item className="d-none d-lg-block">
                  <Button color="inherit">
                    <Link
                      exact
                      to="/medfrontend/auth/login"
                      style={{ fontSize: "2em", color: "black" }}
                    >
                      Sign in
                    </Link>
                  </Button>
                </Grid>
                <Grid item>
                  <Button color="inherit">
                    <Link
                      exact
                      to="/medfrontend/auth/register"
                      style={{ fontSize: "2em", color: "black" }}
                    >
                      Sign up
                    </Link>
                  </Button>
                </Grid>
                <Grid item xs={2} md={2} lg={2}></Grid>
                <hr/>
                 </Toolbar>
              </Grid>
           
          
            
          </AppBar>
        </div>
      );
    } else {
      return (

        <div >
          <AppBar position="static" style={{ backgroundColor: "#FFFF", paddingBottom: "5em", boxShadow:"none" }}>
            
              <Grid container xs={12} md={12} lg={12} style={{position: "fixed", backgroundColor: "inherit", display: "block", zIndex: "100"}}>
              <Toolbar style={{ flexGrow: 1, borderBottom: "1px solid black" , borderColor: "lightgray" }}>
                <Grid item xs={2} md={2} lg={2}></Grid>
                <Grid item xs={2} md={2} lg={2}>
                  {" "}
                  <Typography variant="h3" style={{ color: "black"}}>
                    <Link
                      exact
                      to="/medfrontend/"
                      style={{ fontWeight: "bold", color: "black" }}
                    >
                      Med-book
                    </Link>
                  </Typography>
                </Grid>
                <Grid item xs={4} md={4} lg={4} ></Grid>
                <Nav className="ml-auto">
              {/*<Nav.Item className="ml-auto" bsPrefix="nav-link">
              
            </Nav.Item>*/}
              <NavDropdown
                title={
                  <img
                    src={this.props.imageUrl}
                    style={{
                      width: "40px",
                      height: "40px",
                      borderRadius: "50%",
                    }}
                    alt="Your profile pic"
                  ></img>
                }
                alignRight={true}
                id="dropdown-menu-align-right"
                flip
              >
                <NavDropdown.Item>
                  <Link to={"/medfrontend/user/profile/" + this.props.userId}>
                    Welcome {this.props.userName}
                  </Link>
                </NavDropdown.Item>

                <NavDropdown.Item>
                  <Link to="/medfrontend/user/settings">Settings</Link>
                </NavDropdown.Item>

                <NavDropdown.Item>
                  <Link to="/medfrontend/post/new">New Post</Link>
                </NavDropdown.Item>

                <NavDropdown.Item>
                  <Link onClick={this.logout}>Logout</Link>
                </NavDropdown.Item>
              </NavDropdown>
            </Nav>
                <hr/>
                 </Toolbar>
              </Grid>
           
              

          </AppBar>
        </div>
      );
    }
  }
}

const mapStateToProps = (state) => ({
  isLogged: state.user.isLogged,
  userName: state.user.userName,
  imageUrl: state.user.imageUrl,
  userId: state.user.userId,
});

export default connect(mapStateToProps, { updateUser, logoutUser })(Navig);
